<template>
	<div class="bg-white px-3" style="margin: -20px;margin-top: -1rem;margin-bottom: 0!important;">
		<button-search ref="buttonSearch" class="pt-3" placeholder="要搜索的商品名称" @search="searchEvent">
		<!-- 左边 -->
			<template #left>
				<el-button size="mini" type="danger" @click="deleteAll()">批量删除</el-button>
			</template>
			<template #form>
				<el-form inline ref="form" :model="form"
				label-width="80px">
					<el-form-item label="评价用户" class="mb-0">
						<el-input v-model="form.username" 
						placeholder="评价用户"
						size="mini"></el-input>
					</el-form-item>
					<el-form-item label="评价类型" class="mb-0">
						<el-select v-model="form.type" size="mini"
						placeholder="评价类型">
							<el-option label="区域一" 
							value="shanghai"></el-option>
							<el-option label="区域二" 
							value="beijing"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="发布时间" class="mb-0">
						<el-date-picker
						  v-model="form.time"
						  size="small"
						  type="daterange"
						  range-separator="至"
						  start-placeholder="开始日期"
						  end-placeholder="结束日期">
						</el-date-picker>
					</el-form-item>
					<el-form-item class="mb-0">
						<el-button type="info" size="mini"
						@click="searchEvent">
							搜索</el-button>
						<el-button size="mini"
						@click="clearSearch">清空筛选条件</el-button>
					</el-form-item>
				</el-form>
			</template>
		</button-search>
	
	
		<el-table border class="mt-3"
		  :data="tableData"
		  style="width: 100%"
		   @selection-change="handleSelectionChange">
		  <el-table-column
			type="selection"
			width="45"
			align="center">
		  </el-table-column>
		  
		  <el-table-column
			label="规格名称"
			prop="name">
		  </el-table-column>
		  <el-table-column
			prop="value" 
			align="center"
			width="380"
			label="规格值">
		  </el-table-column>
		  <el-table-column
			align="center"
			prop="order"
			label="排序">
		  </el-table-column>
		  <el-table-column
			prop="status"
			align="center"
			label="状态">
			<template slot-scope="scope">
				<el-button
				:type="scope.row.status ? 'success' : 'danger'" 
				size="mini"
				@click="changeStatus(scope.row)"
				plain>{{scope.row.status ? '启用' : '禁用'}}
				</el-button>
			</template>
		  </el-table-column>
		  <el-table-column
			align="center"
			label="操作"
			width="150">
			<template slot-scope="scope">
				<el-button-group>
				  <el-button type="primary" size="mini" 
				  plain @click="openModel(scope)">修改</el-button>
				  <el-button type="danger" size="mini" 
				  plain @click="deleteItem(scope)"
				  >删除</el-button>
				</el-button-group>
			</template>
		  </el-table-column>
		</el-table>
		<div style="height: 60px;"></div>
		<el-footer class="border-top d-flex align-items-center px-0 position-fixed bg-white" style="bottom: 0;left: 200px;right: 0;z-index: 100;">
		  <div style="flex: 1;" class="px-2">
			  <el-pagination
			  :current-page="currentPage"
			  :page-sizes="[100, 200, 300, 400]"
			  :page-size="100"
			  layout="total, sizes, prev, pager, next, jumper"
			  :total="400">
			</el-pagination>
		  </div>
		</el-footer>

	</div>
</template>

<script>
	import buttonSearch from "@/components/common/button-search.vue"
	export default {
		components: {
			buttonSearch
		},
		data() {
			return {
				tableData: [{
					id:1,
					name:"颜色1",
					value:"棕色,蓝色",
					order:50,
					status:1,
					type:0
				},{
					id:2,
					name:"颜色2",
					value:"棕色,蓝色",
					order:50,
					status:1,
					type:0
				},{
					id:3,
					name:"颜色3",
					value:"棕色,蓝色",
					order:50,
					status:1,
					type:0
				},{
					id:4,
					name:"颜色4",
					value:"棕色,蓝色",
					order:50,
					status:1,
					type:0
				},{
					id:5,
					name:"颜色5",
					value:"棕色,蓝色",
					order:50,
					status:1,
					type:0
				}],
				currentPage:1,
				multipleSelection: [],
				form:{
					username:"",
					type:"",
					time:""
				}
			}
		},
		created() {

		},
		methods: {
			clearSearch(){
				
			},
			searchEvent(e){
				console.log(e);
			},
			// 批量删除
			deleteAll(){
				this.$confirm('是否要删除选中规格?', '提示', {
					confirmButtonText: '删除',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.multipleSelection.forEach(item=>{
						let index = this.tableData.findIndex(v=>v.id === item.id)
						if (index !== -1) {
							this.tableData.splice(index,1)
						}
					})
					this.multipleSelection = []
					this.$message({
						message: '删除成功',
						type: 'success'
					});
				})
			},
			// 选中
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
		},
	}
</script>

<style>
</style>
